import {createSelector} from "reselect";
import {IApplicationState} from "src/~store/models/ApplicationState";

const chatSelector = (state: IApplicationState) => state.chat;

export const messagesSelector = createSelector(chatSelector, chat =>
    chat.messages.sort((m1, m2) => m1.createdDate.getTime() - m2.createdDate.getTime())
);
export const isGetAnswerInProgressSelector = createSelector(chatSelector, chat => chat.isGetAnswerInProgress);



