import * as React from "react";

import {IAnswer} from "../../~store/models/Answer";
import {IAnswerMessageBodyProps} from "./~types/AnswerMessageBodyProps";

import Button from "@material-ui/core/Button";

import "./AnswerMessageBody.css";

export default class AnswerMessageBody extends React.Component<IAnswerMessageBodyProps> {
    public render() {
        const sortedAnswers = this.props.answerMessage.answers.sort((a1, a2) => a2.confidence - a1.confidence);

        const bestMatchAnswers = sortedAnswers.filter(a => a.confidence >= 60 && a.confidence <= 100);
        const averageMatchAnswers = sortedAnswers.filter(a => a.confidence >= 30 && a.confidence < 60);

        if (bestMatchAnswers.length) {
            return this._buildBestMatchProposedMessage(bestMatchAnswers);
        }

        if (averageMatchAnswers.length) {
            return this._buildAverageMatchProposedMessage(averageMatchAnswers);
        }

        return "Ваш вопрос добавлен на обучение!";
    }

    private _onSelectProposedQuestionHandler = (answerText: string): void => {
        this.props.onSelectProposedQuestion(answerText);
    };

    private _getHeadlinerButtons = (answers: IAnswer[]): Array<React.ReactElement<{}>> => {
        return answers.map((a, index) => (
            <Button key={index} className="answer-message-body__button" onClick={() => this._onSelectProposedQuestionHandler(a.text)}>
                {a.headliner}
            </Button>
        ));
    };

    private _getRephraseQuestionButton = (): React.ReactElement<{}> => {
        return (
            <Button
                key="rephrase-question"
                className="answer-message-body__button"
                onClick={() => this._onSelectProposedQuestionHandler("Задайте вопрос")}>
                Перефразировать вопрос
            </Button>
        );
    };

    private _buildProposedMessage = (buttons: Array<React.ReactElement<{}>>): React.ReactElement<{}> => {
        return (
            <div className="answer-message-body">
                <div>Возможно, вы имели ввиду:</div>
                {buttons}
            </div>
        );
    };

    private _buildBestMatchProposedMessage = (bestMatchAnswers: IAnswer[]): React.ReactElement<{}> => {
        if (bestMatchAnswers.length > 1) {
            const buttons = this._getHeadlinerButtons(bestMatchAnswers);
            return this._buildProposedMessage(buttons);
        }

        return <React.Fragment>{bestMatchAnswers[0].text}</React.Fragment>;
    };

    private _buildAverageMatchProposedMessage = (averageMatchAnswers: IAnswer[]): React.ReactElement<{}> => {
        if (averageMatchAnswers.length > 1) {
            const buttons = this._getHeadlinerButtons(averageMatchAnswers);
            const rephraseButton = this._getRephraseQuestionButton();

            return this._buildProposedMessage([...buttons, rephraseButton]);
        }

        return <React.Fragment>{averageMatchAnswers[0].text}</React.Fragment>;
    };
}



