import * as React from 'react';
import {IMessageAvatarProps} from './~types/MessageAvatarProps';

import './MessageAvatar.css';

export class MessageAvatar extends React.Component<IMessageAvatarProps>{
    public render(){
        return(
            <img 
                height={this.props.size} 
                width={this.props.size}
                src={require("../../../../shared/images/test_avatar.png")}
                className={"message-avatar " + this.props.className} />
        );
    }
}


