import {IAnswer} from "../~store/models/Answer";
import {IApiActionResult} from "./models/ApiActionResult";
import {SEARCH_ANSWER_API_ROUTE} from "./apiRoutes";

export const getAnswersAsync = async (topicExternalId: string, questionText: string): Promise<IApiActionResult<IAnswer[]>> => {
    const fetchPromise = fetch(
        `${SEARCH_ANSWER_API_ROUTE}?topicExternalId=${encodeURIComponent(topicExternalId)}&questionText=${encodeURIComponent(questionText)}`
    );
    return await getApiActionResult(fetchPromise, true);
};

const getApiActionResult = async <T>(fetchPromise: Promise<any>, hasReturnValue: boolean = false): Promise<IApiActionResult<T>> => {
    try {
        const response = await fetchPromise;
        const value = hasReturnValue ? await response.json() : null;
        return {isSuccess: true, value};
    } catch {
        return {isSuccess: false};
    }
};



