import {IChatState} from "./ChatState";
import {MessageType} from "../models/messages/enums/MessageType";
import {IAnswerMessage} from "../models/messages/AnswerMessage";

export const initChatState: IChatState = {
    messages: [
        {
            answers: [
                {
                    text: "Задайте Ваш вопрос",
                    confidence: 100
                }
            ],
            type: MessageType.Answer,
            createdDate: new Date()
        } as IAnswerMessage
    ],
    isGetAnswerInProgress: false
};



