import * as React from "react";
import {connect} from "react-redux";
import {animateScroll} from "react-scroll";
import * as queryString from "query-string";

import {IChatProps} from "./~types/ChatProps";
import {MessageType} from "../~store/models/messages/enums/MessageType";
import {IBaseMessage} from "../~store/models/messages/BaseMessage";
import {IAnswerMessage} from "../~store/models/messages/AnswerMessage";
import {IQuestionMessage} from "../~store/models/messages/QuestionMessage";
import {IApplicationState} from "src/~store/models/ApplicationState";
import {addAnswer, addQuestion, getAnswer} from "../~store/actions/actions";
import {messagesSelector, isGetAnswerInProgressSelector} from "../~store/selectors";

import Card from "@material-ui/core/Card";
import Message from "./Message/Message";
import CardHeader from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import MessageInput from "./message-input/MessageInput";
import CircularProgress from "@material-ui/core/CircularProgress";
import AnswerMessageBody from "./answer-message-body/AnswerMessageBody";
import QuestionMessageBody from "./question-message-body/QuestionMessageBody";
import Link from "@material-ui/core/Link";

import "./Chat.css";

type ReduxType = ReturnType<typeof mapStateToProps> & IChatProps;

class Chat extends React.Component<ReduxType> {
    public componentDidMount() {
        this._scrollToBottom();
    }

    public componentDidUpdate() {
        this._scrollToBottom();
    }

    public render() {
        return <section className="chat">{this._getChatControl()}</section>;
    }

    private _scrollToBottom = (): void => {
        animateScroll.scrollToBottom({
            delay: 100,
            duration: 300,
            containerId: "chat-body"
        });
    };

    private _onSendMessageHandler = (messageText: string): void => {
        const params = queryString.parse(this.props.location.search);
        const topicExternalId = params.id as string || "";

        this.props.addQuestion(messageText);
        this.props.getAnswer(topicExternalId, messageText);
    };

    private _onSelectProposedQuestionHandler = (answerText: string): void => {
        this.props.addAnswer(answerText);
    };

    private _getChatControl = (): React.ReactElement<{}> => {
        const {messages} = this.props;

        return (
            <Card className="chat__content">
                <CardHeader color="primary" title={this._getCardHeaderContent()} className="chat__header" />
                <CardContent id="chat-body" className="chat__body">
                    {messages.map((m: IBaseMessage, index: number) => (
                        <Message key={index} className={this._getMessageClassName(m.type)} isShowAvatar={m.type === MessageType.Answer} createdDate={m.createdDate}>
                            {this._getMessageBody(m)}
                        </Message>
                    ))}
                    {this.props.isGetAnswerInProgress && <CircularProgress className="chat__loader" />}
                </CardContent>
                <CardActions className="chat__footer">
                    <MessageInput onSendMessage={this._onSendMessageHandler} />
                    <Link className="chat__content-footer--link" href="http://it-symphony.com/" target="_blank">
                        IT-Symphony
                    </Link>
                </CardActions>
            </Card>
        );
    };

    private _getCardHeaderContent = (): React.ReactElement<{}> => {
        return (
            <React.Fragment>
                <div className="chat__header-content">
                    <img
                        className="chat__avatar"
                        height="50px"
                        width="50px"
                        src="https://cdn4.iconfinder.com/data/icons/scientific-research-and-laboratory-studies/150/icons-66-512.png"
                    />
                    <div className="chat__title">Ask your question</div>
                </div>
            </React.Fragment>
        );
    };

    private _getMessageBody = (message: IBaseMessage): React.ReactElement<{}> => {
        switch (message.type) {
            case MessageType.Question: {
                const questionMessage = message as IQuestionMessage;
                return <QuestionMessageBody questionMessage={questionMessage} />;
            }
            case MessageType.Answer: {
                const answerMessage = message as IAnswerMessage;
                return <AnswerMessageBody answerMessage={answerMessage} onSelectProposedQuestion={this._onSelectProposedQuestionHandler} />;
            }
            default:
                throw new Error("Unsupported message type");
        }
    };

    private _getMessageClassName = (messageType: MessageType): string => {
        switch (messageType) {
            case MessageType.Answer:
                return "message__answer";
            case MessageType.Question:
                return "message__question";
            default:
                throw new Error("Unsupported message type");
        }
    };
}

const mapStateToProps = (state: IApplicationState) => {
    return {
        messages: messagesSelector(state),
        isGetAnswerInProgress: isGetAnswerInProgressSelector(state)
    };
};

export default connect(mapStateToProps, {addAnswer, addQuestion, getAnswer})(Chat);



