import * as React from "react";

import {IMessageProps} from "./~types/MessageProps";

import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";

import {MessageAvatar} from './MessageAvatar/MessageAvatar';

import "./Message.css";

export default class Message extends React.Component<IMessageProps> {
    public render() {
        return (
            <div className={this.props.className}>
                {this.props.isShowAvatar && <MessageAvatar size={30} src="" className="message__avatar"/>}
                <Paper className="message__content">
                    {this.props.children}
                    <Typography className="message__created-date" variant="subtitle2" gutterBottom={true} align="right">
                        {this.props.createdDate.toLocaleString()}
                    </Typography>
                </Paper>
            </div>
        );
    }
}



