import {AppThunkAction} from "src/~store/models/AppThunkAction";
import {getAnswersAsync} from "../../~api/actions";

import {AddAnswerAction, AddQuestionAction, GetAnswersAction} from "./interfaces";
import {ADD_ANSWER, ADD_QUESTION, GET_ANSWERS_START, GET_ANSWERS_SUCCESS, GET_ANSWERS_ERROR} from "./types";

export const addAnswer = (answerText: string): AppThunkAction<AddAnswerAction> => dispatch => {
    dispatch({type: ADD_ANSWER, payload: {answerText}});
};

export const addQuestion = (questionText: string): AppThunkAction<AddQuestionAction> => dispatch => {
    dispatch({type: ADD_QUESTION, payload: {questionText}});
};

export const getAnswer = (topicExternalId: string, questionText: string): AppThunkAction<GetAnswersAction> => async dispatch => {
    dispatch({type: GET_ANSWERS_START});

    const result = await getAnswersAsync(topicExternalId, questionText);
    if (result.isSuccess) {
        const answers = result.value ? result.value : [];
        dispatch({type: GET_ANSWERS_SUCCESS, payload: {answers}});
    } else {
        dispatch({type: GET_ANSWERS_ERROR});
    }
};



