import {Action, Reducer} from "redux";

import {IChatState} from "./state/ChatState";
import {MessageType} from "./models/messages/enums/MessageType";
import {initChatState} from "./state/InitChatState";
import {IAnswerMessage} from "./models/messages/AnswerMessage";
import {IQuestionMessage} from "./models/messages/QuestionMessage";

import {GET_ANSWERS_START, GET_ANSWERS_SUCCESS, GET_ANSWERS_ERROR, ADD_QUESTION, ADD_ANSWER} from "./actions/types";
import {AddQuestionAction, GetAnswersAction, GetAnswersSussessAction, AddAnswerAction} from "./actions/interfaces";

export type KnownAction = AddQuestionAction | GetAnswersAction;

export const chatReducer: Reducer<IChatState> = (state: IChatState = initChatState, incomingAction: Action): IChatState => {
    const action = incomingAction as KnownAction;

    switch (action.type) {
        case ADD_ANSWER: {
            const answerText = (action as AddAnswerAction).payload.answerText;
            const message = {
                answers: [
                    {
                        text: answerText,
                        confidence: 100
                    }
                ],
                type: MessageType.Answer,
                createdDate: new Date()
            } as IAnswerMessage;
            return {...state, messages: [...state.messages, message]};
        }

        case ADD_QUESTION: {
            const questionText = (action as AddQuestionAction).payload.questionText;
            const message = {
                questionText,
                type: MessageType.Question,
                createdDate: new Date()
            } as IQuestionMessage;
            return {...state, messages: [...state.messages, message]};
        }

        case GET_ANSWERS_START: {
            return {...state, isGetAnswerInProgress: true};
        }

        case GET_ANSWERS_SUCCESS: {
            const answers = (action as GetAnswersSussessAction).payload.answers;

            const message = {
                type: MessageType.Answer,
                answers,
                createdDate: new Date()
            } as IAnswerMessage;

            return {...state, messages: [...state.messages, message], isGetAnswerInProgress: false};
        }

        case GET_ANSWERS_ERROR: {
            return {...state, isGetAnswerInProgress: false};
        }

        default:
            return state;
    }
};



